import Axios from "./axios"  // 导入配置好的axios文件
import qs from  "qs"

export function queryIcIcBankRecordPageList(datas) {
  return Axios({
    url: "/icIcBankRecord/queryIcIcBankRecordPageList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}

export function matchPayLog(datas) {
  return Axios({
    url: "/icIcBankRecord/matchPayLog",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}






