import Axios from "./axios"  // 导入配置好的axios文件
import qs from  "qs"

export function getSettings() {
  return Axios({
    url: "/platformSettings/getSettings",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    }
  })
}



