<template>
  <div id="app">
    <template>
      <el-tabs v-model="active" @tab-click="handleClick">
        <el-tab-pane v-for="(item,index) in countryOptions" :key="item.value" :label="item.label" :name="item.label"
                     @click="changeList(item)">{{ item.name }}
        </el-tab-pane>
      </el-tabs>
    </template>
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="商户" prop="name">
        <el-input v-model="searchForm.name" placeholder="通道ID或名称"></el-input>
      </el-form-item>
      <el-form-item label="状态" prop="status">
        <el-select v-model="searchForm.status" placeholder="请选择">
          <el-option label="请选择" value=""></el-option>
          <el-option label="启用" value="1"></el-option>
          <el-option label="禁用" value="-1"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="提现方式" prop="type">
        <el-select v-model="searchForm.type" multiple placeholder="请选择">
          <el-option label="银行卡" value="1"></el-option>
          <el-option label="电子钱包" value="2"></el-option>
          <el-option label="UPI" value="3"></el-option>
          <el-option label="PIX" value="4"></el-option>
          <el-option label="CLABE" value="4"></el-option>
          <el-option label="HTML" value="7"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search">查询</el-button>
        <el-button @click="resetForm('searchForm')">重置</el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <el-button plain @click="dialog.visible=true">添加通道</el-button>
    </el-row>
    <el-table
      :row-class-name="tableRowClassName"
      :data="tableData" size="mini"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="配置参数">
              <span>{{ props.row.params }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="id"
        label="通道ID"
        align="center">
      </el-table-column>
      <el-table-column prop="name" label="通道名称" align="center">
      </el-table-column>
      <el-table-column prop="outName" label="别名" align="center">
      </el-table-column>
      <el-table-column label="手续费" align="center">
        <template slot-scope="scope">
          <el-tag type="success" size="mini">
            {{ Number(scope.row.feeFix / 100).toFixed(2) }}+{{
              Number(scope.row.feeRate * 100).toFixed(2)
            }}%
          </el-tag>
        </template>
      </el-table-column>

      <el-table-column prop="sort" label="权重" align="center">
      </el-table-column>
      <el-table-column label="充值方式" align="center">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.types && scope.row.types.indexOf('1')!==-1" size="mini">银行卡</el-tag>
          <el-tag type="info" v-if="scope.row.types && scope.row.types.indexOf('2')!==-1" size="mini">电子钱包</el-tag>
          <el-tag type="success" v-if="scope.row.types && scope.row.types.indexOf('3')!==-1" size="mini">UPI
          </el-tag>
          <el-tag type="success" v-if="scope.row.types && scope.row.types.indexOf('7')!==-1" size="mini">HTML
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="单笔限额" prop="range" align="center">
      </el-table-column>
      <el-table-column label="货币" prop="currency" align="center">
      </el-table-column>
      <el-table-column label="创建时间" prop="createTime" :formatter="dateFormat" align="center">
      </el-table-column>
      <el-table-column label="启用/禁用通道" align="center">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.status==1?'禁用':'启用'" placement="top">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @change="changeStatus(scope.row)">
            </el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="300" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
          <el-button size="mini" @click="handlePayTest(scope.$index, scope.row)">支付测试</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="page.total" :current-page="page.cur" @current-change="pageChange">
      </el-pagination>
    </div>
    <!-- 新增或修改通道 -->
    <el-dialog :title="this.dialog.isInsert? '新增通道' : '修改通道'" :visible.sync="dialog.visible" @closed="closeDialog">
      <el-dialog
        title="提示"
        :visible.sync="Successdialog"
        :show-close=false
        :close-on-click-modal=true
        :close-on-press-escape=false
        width="30%"
        center
      >
        <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="Successdialog = false">立即关闭</el-button>
      </span>
      </el-dialog>
      <el-form :model="dialog.form" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
        <el-form-item label="通道名称" prop="name">
          <el-input v-model="dialog.form.name"></el-input>
        </el-form-item>
        <el-form-item label="通道别名" prop="outName">
          <el-input v-model="dialog.form.outName"></el-input>
        </el-form-item>
        <el-form-item label="接口参数" prop="params">
          <el-input type="textarea" autosize v-model="dialog.form.params"></el-input>
        </el-form-item>
        <el-form-item label="手续费（固定）" prop="feeFix">
          <el-input v-model="dialog.form.feeFix"></el-input>
        </el-form-item>
        <el-form-item label="手续费（费率）" prop="feeRate">
          <el-input v-model="dialog.form.feeRate"></el-input>
        </el-form-item>
        <el-form-item label="权重" prop="sort">
          <el-input-number v-model="dialog.form.sort" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="充值方式" prop="type">
          <el-checkbox-group v-model="dialog.form.typesArr">
            <el-checkbox label="1" name="type">银行卡</el-checkbox>
            <el-checkbox label="2" name="type">电子钱包</el-checkbox>
            <el-checkbox label="3" name="type">UPI</el-checkbox>
            <el-checkbox label="7" name="type">HTML</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="工作时间" prop="workTime">
          <el-input v-model="dialog.form.workTime"></el-input>
        </el-form-item>
        <el-form-item label="单笔限额" prop="range">
          <el-tag type="info" v-if="!dialog.isInsert">上游：{{ dialog.form.thirdRange }}</el-tag>
          <el-input v-model="dialog.form.range" placeholder="例：100-500000"></el-input>
        </el-form-item>
        <el-form-item label="上游限额" prop="thirdRange" v-if="dialog.isInsert">
          <el-input v-model="dialog.form.thirdRange" placeholder="例：100-500000"></el-input>
        </el-form-item>
        <el-form-item label="货币类型" prop="currency">
          <el-select v-model="dialog.form.currency" placeholder="请选择">
            <el-option v-for="(item,index) in this.currencyOptions" :label="item.value" :value="item.value"
                       :key="item.value">{{ item.value }}
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button size="medium" icon="el-icon-search" type="primary" @click="submitForm('ruleForm')">提交</el-button>
          <el-button v-if="this.dialog.isInsert" size="medium" icon="el-icon-refresh-left"
                     @click="resetForm('ruleForm')">重置
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!-- 支付测试 -->
    <el-dialog title="通道信息" :visible.sync="dialogTest.visible">
      <!--            <el-form :model="dialogTest.form" :rules="rulesTest" ref="ruleTestForm" label-width="150px" class="demo-TestForm">-->
      <!--                <el-form-item label="支付链接" prop="payUrl">-->
      <!--                    <el-input v-model="dialogTest.form.payUrl"></el-input>-->
      <!--                </el-form-item>-->
      <!--            </el-form>-->
      <el-dialog
        title="提示"
        :visible.sync="Successdialog"
        :show-close=false
        :close-on-click-modal=false
        :close-on-press-escape=false
        :modal='false'
        width="30%"
        center>
            <span v-if="testPayUrl" style="font-size:16px;">获取到支付链接：<span
              style="color: #01AAED">{{ testPayUrl }}</span></span>
        <span v-if="testPayNumber" style="font-size:16px;">获取到转账账号:{{ testPayNumber }}</span>
        <span slot="footer" class="dialog-footer">
        <el-button @click="Successdialog = false">取消</el-button>
        <el-button type="primary" @click="returnPayUrl()">立即跳转</el-button>
      </span>
      </el-dialog>
      <el-form ref="form" :model="dialogTest.form" label-width="90px">
        <el-form-item label="测试商户Id">
          <el-input v-model="dialogTest.form.appId"></el-input>
        </el-form-item>
        <el-form-item label="金额">
          <el-input v-model="dialogTest.form.amount"></el-input>
        </el-form-item>
        <el-form-item label="支付方式">
          <el-select v-model="dialogTest.form.type" placeholder="请选择活动区域">
            <el-option label="银行卡" value="1"></el-option>
            <el-option label="电子钱包" value="2"></el-option>
            <el-option label="UPI" value="3"></el-option>
            <el-option label="TRC20" value="4"></el-option>
            <el-option label="HTML" value="7"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <el-button @click="dialogTest.visible = false">取 消</el-button>
      <el-button type="primary" @click="testPay()">确 定</el-button>
    </el-dialog>
  </div>
</template>

<script>
import {
  addPayType,
  changePayTypeStatus,
  deletePayType,
  queryPayTypePageList,
  testPay,
  updatePayType
} from "../../api/paytype"
import {dateFormate} from "../../statics/custom";
import Axios from "../../api/axios";
import {queryCountryPageList, queryCurrencyPageList} from "../../api/dictionary";

export default {
  name: "payTypeList",
  data() {
    let isNumber = (rule, value, callback) => {
      if (typeof value == 'number') {
        callback();
        return;
      }
      if (value == null || value.indexOf(' ') !== -1) {
        callback(new Error('必须是数字!'));
        return;
      }
      if (!isNaN(value)) {
        callback();
      } else {
        callback(new Error('必须是数字!'));
      }
    }
    return {
      active: '印度',
      country: '',
      fullscreenLoading: true,
      tableData: [],
      types: [],
      testPayVisible: false,
      Successdialog: false,
      isDisabled: false,
      Sencond: 10,
      testPayUrl: '',
      testPayNumber: '',
      page: {
        pageSize: 10,
        total: 0,
        cur: 1
      },
      type: [],
      dialog: {
        isInsert: true,
        visible: false,
        form: {
          name: '',
          outName: '',
          params: '',
          feeFix: '',
          feeRate: '',
          sort: '',
          typesArr: [],
          types: '',
          workTime: '',
          range: '',
          currency: '',
          thirdRange: ''
        }
      },
      dialogTest: {
        isInsert: true,
        visible: false,
        form: {
          appId: '',
          amount: '',
          type: '',
          payTypeId: ''
        }
      },
      searchForm: {
        name: '',
        stauts: '',
        type: [],
        types: '',
        country: ''
      },
      rulesTest: {
        Name: [
          {required: true, message: '请输入通道名称', trigger: 'blur'},
        ],
        Params: [
          {required: true, message: '请输入接口参数', trigger: 'blur'},
        ],
        FeeFix: [
          {validator: isNumber, trigger: 'blur'},
        ],
        FeeRate: [
          {validator: isNumber, trigger: 'blur'},
        ],
        Type: [
          {type: 'array', required: true, message: '请至少选择一种方式', trigger: 'change'}
        ],
        Currency: [
          {required: true, message: '请选择货币种类', trigger: 'change'}
        ],
        Range: [
          {required: true, message: '请输入单笔限额', trigger: 'blur'},
        ],
        ThirdRange: [
          {required: true, message: '请输入上游单笔限额', trigger: 'blur'},
        ]
      },
      rules: {
        name: [
          {required: true, message: '请输入通道名称', trigger: 'blur'},
        ],
        outName: [
          {required: true, message: '请输入通道别名', trigger: 'blur'},
        ],
        params: [
          {required: true, message: '请输入接口参数', trigger: 'blur'},
        ],
        feeFix: [
          {validator: isNumber, trigger: 'blur'},
        ],
        feeRate: [
          {validator: isNumber, trigger: 'blur'},
        ],
        // type: [
        //     {type: 'array', required: true, message: '请至少选择一种方式', trigger: 'change'}
        // ],
        currency: [
          {required: true, message: '请选择货币种类', trigger: 'change'}
        ],
        range: [
          {required: true, message: '请输入单笔限额', trigger: 'blur'},
        ],
        thirdRange: [
          {required: true, message: '请输入上游单笔限额', trigger: 'blur'},
        ]
      },

      countryOptions: [],
      currencyOptions: []
    }
  },
  async created() {
    this.searchForm.country = 'IND'
    this.queryCountryPageList()
    this.queryCurrencyPageList()
    await this.refresh()
  },
  methods: {
    refresh() {
      let that = this
      this.searchForm.types = this.dialog.form.typesArr.sort(function (a, b) {
        return a - b
      }).join(',')
      queryPayTypePageList({
        page: this.page.cur,
        limit: this.page.pageSize,
        status: this.searchForm.status,
        name: this.searchForm.name,
        types: this.searchForm.types,
        country: this.searchForm.country
      }).then(function (response) {
        that.fullscreenLoading = false
        if (response.data.code === 0) {
          that.tableData = response.data.data
          that.page.total = response.data.count
        }
      })
    },
    dateFormat(row, column) {
      let date = row[column.property];
      if (date === undefined || date == null) {
        return ''
      }
      return dateFormate(date)
    },
    pageChange(cur) {
      this.page.cur = cur
      this.refresh()
    },
    tableRowClassName({row, rowIndex}) {
      if (row.status === 1) {
        return 'success-row'
      } else {
        return 'warning-row'
      }
    },
    handleEdit(index, row) {
      this.dialog.form.id = row.id
      this.dialog.form.name = row.name
      this.dialog.form.outName = row.outName
      this.dialog.form.params = row.params
      this.dialog.form.feeRate = row.feeRate
      this.dialog.form.sort = row.sort
      this.dialog.form.workTime = row.workTime
      this.dialog.form.range = row.range
      this.dialog.form.currency = row.currency
      this.dialog.form.thirdRange = row.thirdRange
      this.dialog.form.types = row.types
      this.dialog.form.typesArr = String(row.types).split(',')
      this.dialog.form.feeFix = Number(row.feeFix / 100).toFixed(2)
      this.dialog.visible = true
      this.dialog.isInsert = false
    },
    handlePayTest(index, row) {
      if (row.status === 1) {
        this.dialogTest.form.payTypeId = row.id
        console.log('handlePayTest:' + row.id)
        this.dialogTest.visible = true

        // axios({method: 'post',url:'/paytype/testPay.action',params:{
        //         type:type,
        //         amount:amount,
        //         currency:currency,
        //         payTypeId:payTypeId
        //     }}).then(res=>{
        //     if(res.data.code == 0){
        //
        //     }else {
        //         this.$message({
        //             message: res.data.msg,
        //             type: 'warning'
        //         });
        //     }
        // })
      } else {
        this.$message({
          message: '该商户未启用！',
          type: 'warning'
        });
      }
    },
    handleDelete(index, row) {
      let that = this
      this.$confirm('此操作将永久删除通道, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deletePayType({
          id: row.id
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '删除成功',
              type: 'success'
            })
          } else {
            that.$message.error('删除失败');
          }
          that.refresh()
        })
      })
    },
    submitForm(formName) {
      let str = this.dialog.isInsert ? '添加' : '编辑'
      if (this.dialog.isInsert) {
        delete this.dialog.form.id
      }
      let that = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$confirm('确定' + str + '通道?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            that.dialog.form.types = String(that.dialog.form.typesArr).replace('[', '').replace(']', '')
            const resp = (that.dialog.isInsert ? (addPayType(that.dialog.form)) : (updatePayType(that.dialog.form)))
            resp.then((response) => {
              if (response.data.code === 0) {
                that.$message({
                  message: str + '成功',
                  type: 'success'
                })
              } else {
                that.$message.error(str + '失败');
              }
              that.dialog.form.typesArr = []
              that.refresh()
              that.closeDialog()
            })
          }).catch(() => {
          })
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    beforeCloseDialog() {
      console.log(1)
      this.$refs['ruleForm'].clearValidate()
      this.$refs['ruleForm'].resetFields()
    },
    closeDialog() {
      let obj = this.dialog.form
      Object.keys(obj).forEach(key => {
        let type = typeof obj[key]
        if (type === 'number') {
          obj[key] = 0
        }
        if (type === 'string') {
          obj[key] = ''
        }
      })
      obj.stauts = -1
      this.dialog.visible = false
      this.dialog.isInsert = true
      this.$refs.ruleForm.clearValidate()
      // this.resetForm('ruleForm')
    },
    search() {
      this.page.cur = 1
      this.refresh()
    },
    testPay() {
      const that = this;
      that.testPayUrl = ''
      that.testPayNumber = ''
      const req = {
        appId: this.dialogTest.form.appId,
        amount: this.dialogTest.form.amount * 100,
        type: this.dialogTest.form.type,
        payTypeId: this.dialogTest.form.payTypeId,
      }
      console.log(req)

      testPay(req).then(function (response) {
        var data = response.data
        if (data.url) {
          var url = data.url
          if (String(url).startsWith("http") || String(url).startsWith("https")) {
            that.$message('获得支付链接成功')
            that.testPayUrl = url
            that.Successdialog = true
            //that.getSencond(url)
          } else {
            that.testPayNumber = url
            that.Successdialog = true
          }
        } else {
          that.$message("此通道请求失败")
        }
      }).catch(function (response) {
      })
    },
    getSencond(data) {
      const that = this
      const interval = window.setInterval(function () {
        --that.Sencond
        if (that.Sencond === 0) {
          //that.isDisabled = false
          window.clearInterval(interval)
          window.open(data)//倒计时结束时运行的业务逻辑，这里的是关闭当前页面
        }
      }, 1000)
    },
    sendMsg() {
      window.close()
    },
    returnPayUrl() {
      window.open(this.testPayUrl)
    },
    changeStatus(row) {
      let that = this
      changePayTypeStatus({
        id: row.id,
        status: row.status
      }).then(function (response) {
        if (response.data.code === 0) {
          that.$message({
            message: '修改成功',
            type: 'success'
          })
        } else {
          that.$message.error('修改失败');
        }
        that.refresh()
      })
    },
    handleClick(tab, event) {
      this.searchForm.country = this.countryOptions[tab.index].value
      this.refresh()
    },
    changeList(item) {
      this.country = item.value
    },
    queryCountryPageList() {
      let that = this
      return new Promise(resolve => {
        queryCountryPageList({type: 'country'}).then(function (res) {
          if (res.data.code === 0) {
            that.countryOptions = []
            res.data.data.forEach((item, index) => {
              that.countryOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
    queryCurrencyPageList() {
      let that = this
      return new Promise(resolve => {
        queryCurrencyPageList({type: 'currency'}).then(function (res) {
          if (res.data.code === 0) {
            that.currencyOptions = []
            res.data.data.forEach((item, index) => {
              that.currencyOptions.push(
                {
                  "label": item.name,
                  "value": item.value
                }
              )
            })
          }
        })
      })
    },
  }
}
</script>

<style scoped>

</style>



